import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import burgerIcon from '../../../assets/images/burger-icon.svg';
import closeIcon from '../../../assets/images/close-icon.svg';
import logo from '../../../assets/images/logo.png';
// import manchesterlogo from '../../../assets/images/manchesterlogo.png';
// import reallogo from '../../../assets/images/reallogo.png';
import arrowright from '../../../assets/images/arrowright.svg';

import moment from "moment";

import './index.css';
import { Button, ToggleButton, ToggleButtonGroup } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import SideMenu from '../../../components/SideMenu';

import { SPORT_ADD_REQUEST } from "../../../store/actionTypes";
import { WIN_POINT_RULES } from '../../../const';

const SelectPoint = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { user } = useSelector((state) => state.auth);
    const { playList, team1Idx, eventDate } = useSelector((state) => state.sport);

    const [menuOpen, setMenuOpen] = useState(false);
    const [gamePoint, setGamePoint] = useState(5);

    const handleMenuToggle = () => {
        setMenuOpen(!menuOpen);
    };

    useEffect(() => {
        if (menuOpen) {
            document.body.style.overflowX = 'hidden';
            document.body.style.overflowY = 'hidden';
            document.documentElement.style.overflowX = 'hidden';
            document.documentElement.style.overflowY = 'hidden';
        } else {
            document.body.style.overflow = '';
            document.documentElement.style.overflow = '';
        }
    }, [menuOpen]);

    const handleGamePoint = (val) => {
        setGamePoint(val)
        dispatch({ type: SPORT_ADD_REQUEST, payload: { point: val } })
    };

    const handleGame = () => {
        if (isNaN(user.payment)) {
            navigate('/payments')
        } else {
            if (isNaN(user.payment.venmo) && isNaN(user.payment.paypal) && isNaN(user.payment.cashApp) && isNaN(user.payment.zelle)) {
                navigate('/payments')
            } else {
                navigate('/play')
            }
        }
        dispatch({ type: SPORT_ADD_REQUEST, payload: { point: gamePoint } })
    }

    // console.log(sport, 'sport')
    return (
        <>
            <div className={`screen-wrapper ${menuOpen ? 'menu-open' : ''}`}>
                <div className='burger-icon' onClick={handleMenuToggle}>
                    <img src={menuOpen ? closeIcon : burgerIcon} alt="menu toggle" />
                </div>
                {/* <div className='srbook'>
                    <img src={srbook} alt="book" className='img-fluid'/>
                    <span>House Rules</span>
                </div> */}
                <div className='screen-3-section'>
                    <div className='screen-3-content'>
                        <div className='screen-top-part'>
                            <div className='logo-part'>
                                <a href='/'>
                                    <img src={logo} alt="logo" className='img-fluid' />
                                </a>
                            </div>
                            <span className='screen-title'>Your Game</span>
                            <div className='select-team-group'>
                                <div className='select-team'>
                                    <div className='team-image'>
                                        {playList.boxscore ? <img src={playList.boxscore.teams[0].team.logo} alt={playList.boxscore && playList.boxscore.teams[0].team.shortDisplayName} style={{ width: 43, height: 43 }} /> : <div></div>}
                                    </div>
                                    <span>{playList.boxscore && playList.boxscore.teams[0].team.shortDisplayName}</span>
                                </div>
                                <div className='opposite-part'>vs <span>{eventDate && moment(eventDate).format("h:mm")}</span></div>
                                <div className='select-team'>
                                    <div className='team-image'>
                                        {/* <img src={reallogo} alt="manchester" className='img-fluid' /> */}
                                        {playList.boxscore ? <img src={playList.boxscore.teams[1].team.logo} alt={playList.boxscore && playList.boxscore.teams[1].team.shortDisplayName} style={{ width: 43, height: 43 }} className='img-fluid' /> : <div></div>}
                                    </div>
                                    <span>{playList.boxscore && playList.boxscore.teams[1].team.shortDisplayName}</span>
                                </div>
                            </div>
                            <div className='your-team-block'>
                                <div className='your-team'>
                                    <h4>Your Team</h4>
                                </div>
                                <div className='select-team'>
                                    <div className='team-image'>
                                        {playList.boxscore ? <img src={playList.boxscore.teams[team1Idx].team.logo} alt={playList.boxscore && playList.boxscore.teams[team1Idx].team.shortDisplayName} style={{ width: 27, height: 27 }} className='img-fluid' /> : <div></div>}
                                    </div>
                                    <span>{playList.boxscore && playList.boxscore.teams[team1Idx].team.shortDisplayName}</span>
                                </div>
                            </div>
                        </div>
                        <div className='select-money-block'>
                            <span className='screen-purple-glow-title'>PayOuts</span>
                            <div className='user-select-money'>
                                <ToggleButtonGroup
                                    type="radio"
                                    name="options"
                                    value={gamePoint}
                                    onChange={handleGamePoint}
                                    className="mb-2"
                                >
                                    <ToggleButton
                                        id="radio-1"
                                        value={5}
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $5
                                    </ToggleButton>
                                    <ToggleButton
                                        id="radio-2"
                                        value={10}
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $10
                                    </ToggleButton>
                                    <ToggleButton
                                        id="radio-3"
                                        value={25}
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $25
                                    </ToggleButton>
                                    <ToggleButton
                                        id="radio-4"
                                        value={50}
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $50
                                    </ToggleButton>
                                    <ToggleButton
                                        id="radio-5"
                                        value={100}
                                        variant="outline-primary"
                                        className="btn-custom"
                                    >
                                        $100
                                    </ToggleButton>
                                </ToggleButtonGroup>
                            </div>
                            <div className='fee-block'>
                                <div className='fee-info'>
                                    <p>${gamePoint} <span>Entry Fee</span></p>
                                </div>
                                <div className=''>
                                    <img src={arrowright} alt="arrow" className='img-fluid' />
                                </div>
                                <div className='fee-info'>
                                    <p>${WIN_POINT_RULES[gamePoint]} <span>To Win</span></p>
                                </div>
                            </div>
                            <div className='submit-btn'>
                                <Button onClick={handleGame}>Submit</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Side Menu */}
            <SideMenu
                menuOpen={menuOpen}
                user={user}
            />
        </>
    )
}

export default SelectPoint
