import React, { useEffect, useState } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import burgerIcon from '../../assets/images/burger-icon.svg';
import closeIcon from '../../assets/images/close-icon.svg';
import profile from '../../assets/images/profile.png';
import user from '../../assets/images/user.svg';
import soccerball from '../../assets/images/soccerball.svg';
import srcard from '../../assets/images/srcard.svg';
import srbook from '../../assets/images/srbook.svg';
import clock from '../../assets/images/clock.svg';
import setting from '../../assets/images/setting.svg';
import Helpinfo from '../../assets/images/Helpinfo.svg';
import logout from '../../assets/images/logout.svg';
import screen8topimage from '../../assets/images/screen8topimage.png';
import mobilescreen from '../../assets/images/mobilescreen.png';
import list from '../../assets/images/list.png';
import { Link, useNavigate } from 'react-router-dom';
import './index.css';
import SideMenu from '../../components/SideMenu';
import { useDispatch, useSelector } from 'react-redux';

const Screen8 = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { user } = useSelector((state) => state.auth);

    const [menuOpen, setMenuOpen] = useState(false);

    const handleMenuToggle = () => {
        setMenuOpen(!menuOpen);
    };

    useEffect(() => {
        if (menuOpen) {
            document.body.style.overflowX = 'hidden';
            document.body.style.overflowY = 'hidden';
            document.documentElement.style.overflowX = 'hidden';
            document.documentElement.style.overflowY = 'hidden';
        } else {
            document.body.style.overflow = '';
            document.documentElement.style.overflow = '';
        }
    }, [menuOpen]);

    return (
        <>
            <div className={`screen-wrapper ${menuOpen ? 'menu-open' : ''}`}>
                <div className='burger-icon' onClick={handleMenuToggle}>
                    <img src={menuOpen ? closeIcon : burgerIcon} alt="menu toggle" />
                </div>
                <div className='screen-8-section'>
                    <div className='screen-8-content'>
                        <div className='screen-8-top-part'>
                            <div className='screen-8-top-image'>
                                <img src={screen8topimage} alt="img" className='img-fluid' />
                            </div>
                            <div className='mobile-screen'>
                                <img src={mobilescreen} alt="mobilescreen" className='img-fluid' />
                            </div>
                            <div className='screen-8-main-title'>
                                <h1>Watch
                                    Play
                                    <span>Win!!</span></h1>
                            </div>
                        </div>
                        <div className='screen-8-center-part'>
                            <div className='screen-8-center-image'>
                                <img src={list} alt="list" className='img-fluid' />
                            </div>
                            <div className='screen-8-center-title'>
                                <h2>SIMPLE,
                                    FAST,
                                    FUN! <span>Win 3.5X!</span></h2>
                            </div>
                        </div>
                        <div className='screen-8-bottom-block'>
                            <div className='screen-8-bottom-part'>
                                <div className='screen-8-bottom-title'>
                                    <h3><span>BALLHOG!</span> iTS DIFFERENT.</h3>
                                </div>
                                <div className='screen-8-bottom-paragraph'>
                                    <p>A new way to play and win. BallHog makes it simple to focus on your team and play fantasy. Play with friends, fellow fans, or by yourself. But Play to WIN!</p>
                                </div>
                                <div className='start-play-btn-block'>
                                    <Link>Start playing today <span><FontAwesomeIcon icon={faArrowRight} /></span></Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Side Menu */}
            <SideMenu
                menuOpen={menuOpen}
                user={user}
            />
        </>
    )
}

export default Screen8