import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import Event from '../components/Event';
import { useDispatch, useSelector } from 'react-redux';

function Playground() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { primaryColor, secondaryColor } = useSelector((state) => state.sport);

  const [tabStatus, setTabStatus] = useState(true);
  // const [primaryColor, setPrimaryColor] = useState('#0d6efd');
  // const [secondaryColor, setSecondaryColor] = useState('white');

  const style = {
    menuContent: {
      position: 'absolute',
      display: 'inline-block',
      right: 11,
      top: 15
    },
    menuContentItem: {
      width: 20,
      height: 3,
      backgroundColor: secondaryColor,
      margin: '5px 0'
    }
  }

  const handleTab = () => {
    setTabStatus(!tabStatus);
  }
  
  const handleGame = () => {
    navigate('/')
  }

  return (
    <>
      <div className="p-2 text-left" style={{ backgroundColor: primaryColor, color: secondaryColor }}>
        <h1 className='text-center text-uppercase w-100' style={{ fontFamily: 'Varsity Font', fontSize: 35 }} onClick={handleGame}>BALLHOG</h1>
        <div style={style.menuContent}>
          <div style={style.menuContentItem}></div>
          <div style={style.menuContentItem}></div>
          <div style={style.menuContentItem}></div>
        </div>
      </div>
      <div className='container px-3'>
        <Event
          primaryColor={primaryColor}
          secondaryColor={secondaryColor}
          tabStatus={tabStatus}
          // setPrimaryColor={setPrimaryColor}
          // setSecondaryColor={setSecondaryColor}
          handleTab={handleTab}
        />
      </div>
    </>
  );
}

export default Playground;
