import React, { useEffect } from 'react';

import profileImg from '../assets/images/profile.png';
import userImg from '../assets/images/user.svg';
import soccerballImg from '../assets/images/soccerball.svg';
import srcardImg from '../assets/images/srcard.svg';
import clockImg from '../assets/images/clock.svg';
import settingImg from '../assets/images/setting.svg';
import HelpinfoImg from '../assets/images/Helpinfo.svg';
import logoutImg from '../assets/images/logout.svg';
import srbookImg from '../assets/images/srbook.svg';
import { Link } from 'react-router-dom';

import { useDispatch, useSelector } from 'react-redux';
import { logoutAct } from '../store/actions/authActions';


const SideMenu = (props) => {
    const { menuOpen, user } = props;
    const dispatch = useDispatch();
    const { token, loading, error } = useSelector((state) => state.auth);

    useEffect(() => {
        if (menuOpen) {
            document.body.style.overflowX = 'hidden';
            document.body.style.overflowY = 'hidden';
            document.documentElement.style.overflowX = 'hidden';
            document.documentElement.style.overflowY = 'hidden';
        } else {
            document.body.style.overflow = '';
            document.documentElement.style.overflow = '';
        }
    }, [menuOpen]);

    const handleLogout = () => {
        console.log(token, 'logout')
        dispatch(logoutAct());
    }
    return (
        <>
            {/* Side Menu */}
            <div className={`side-menu ${menuOpen ? 'open' : ''}`}>
                <div className='side-menu-content'>
                    <div className='profile-photo-block'>
                        {/* <div className='profile-photo'>
                            <img src={profileImg} alt="profile" className='img-fluid' />
                        </div> */}
                        <div className='profile-name-info'>
                            <h4>{user?user.name:''}</h4>
                        </div>
                    </div>
                    <div className='money-balance'>
                        <span>${user?user.balance:'0'}</span>
                    </div>
                    <div className='menu-list'>
                        <ul>
                            <li><Link><img src={userImg} alt="user" className='img-fluid' />My Account</Link></li>
                            <li><Link><img src={soccerballImg} alt="soccerball" className='img-fluid' />Active/Pending Games</Link></li>
                            <li><Link><img src={srcardImg} alt="srcard" className='img-fluid' />Payout Source</Link></li>
                            <li><Link><img src={clockImg} alt="clock" className='img-fluid' />Game History</Link></li>
                            <li><Link><img src={settingImg} alt="setting" className='img-fluid' />Settings</Link></li>
                            <li><Link to='/screen7'><img src={HelpinfoImg} alt="Helpinfouser" className='img-fluid' />Help</Link></li>
                            <li><Link><img src={srbookImg} alt="srbook" className='img-fluid sr-book' />House Rules</Link></li>
                            <li><Link onClick={handleLogout}><img src={logoutImg} alt="logout" className='img-fluid' />Log out</Link></li>
                        </ul>
                    </div>
                </div>
            </div>
        </>
    );
};

export default SideMenu;





