import React from 'react';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
// import Cookies from 'js-cookie'
import 'bootstrap/dist/css/bootstrap.css'
import './assets/App.css';
import './assets/fonts/fonts.css';
import { useSelector } from 'react-redux';

import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


import { Login, Register, Playground, Sports, SelectPoint, FreePoint, Payments, Screen7, Screen8 } from "./pages";

const App = () => {
  const { token, user} = useSelector((state) => state.auth);

  console.log(token, 'token')
  console.log(user, 'user')
  return (
    <>
      <Router>
        <Routes>
          <Route path="/login" element={!token ? <Login /> : <Navigate to="/" />} />
          <Route path="/register" element={!token ? <Register /> : <Navigate to="/" />} />

          <Route path="/" element={token ? <Sports /> : <Navigate to="/login" />} />
          <Route path="/point/select" element={token ? <SelectPoint /> : <Navigate to="/login" />} />
          <Route path="/point/free" element={token ? <FreePoint /> : <Navigate to="/login" />} />
          <Route path="/payments" element={token ? <Payments /> : <Navigate to="/login" />} />
          <Route path="/screen7" element={token ? <Screen7 /> : <Navigate to="/login" />} />
          
          <Route path="/screen8" element={token ? <Screen8 /> : <Navigate to="/login" />} />
          <Route path="/play" element={token ? <Playground /> : <Navigate to="/login" />} />

          {/* Catch-all route for undefined routes */}
          <Route path="*" element={<Navigate to={!token ? "/" : "/login"} />} />
        </Routes>
      </Router>
      <ToastContainer />
    </>
  );
};

export default App;