import React, { useEffect, useState } from 'react'
import { Button, Form } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import './index.css';
import gameimg from '../../assets/images/gameimg.png';
import biglogo from '../../assets/images/biglogo.png';
import message from '../../assets/images/message.svg';
import lock from '../../assets/images/lock.svg';
import facebook from '../../assets/images/facebook.svg';
import google from '../../assets/images/google.svg';
import apple from '../../assets/images/apple.svg';
import hide from '../../assets/images/hide.svg';

import { loginAct } from '../../store/actions/authActions';

const Login = () => {
    const [formData, setFormData] = useState({})
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { token, loading, error } = useSelector((state) => state.auth);

    // Redirect to the index route after successful login
    useEffect(() => {
        if (token) {
            navigate('/'); // Redirect to the index route or desired page
        }
    }, [token, navigate]);

    const handleChange = (e, prop) => {
        let inputData = { ...formData }
        inputData[prop] = e.target.value
        setFormData(inputData);
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        dispatch(loginAct(formData));
        navigate('/login')
    };
    
    return (
        <>
            <div className='screen-1-section'>
                <div className='screen-1-content'>
                    <div className='screen-gameimgpart'>
                        <div className='screen-gameimage'>
                            <img src={gameimg} alt="gameimg" className='img-fluid' />
                        </div>
                        <div className='screen-logo-part'>
                            <div className='screen-1-logo-image'>
                                <img src={biglogo} alt="logo" className='img-fluid' />
                            </div>
                            <div className='screen-1-main-title'>
                                <h3>BallHog</h3>
                            </div>
                        </div>
                    </div>
                    <div className='screen-form'>
                        <Form>
                            <Form.Group controlId="formGroupEmail" className='form-group'>
                                <Form.Control type="email" placeholder="Email Address" onChange={(e) => handleChange(e, 'email')} value={formData['email']} />
                                <img src={message} alt="message" className='img-fluid' />
                            </Form.Group>
                            <Form.Group controlId="formGroupPassword" className='form-group' onChange={(e) => handleChange(e, 'password')} value={formData['password']}>
                                <Form.Control type="password" placeholder="Password" />
                                <img src={lock} alt="lock" className='img-fluid' />
                                <img src={hide} alt="hide" className='hide-password' />
                            </Form.Group>
                            <div className='foregt-password-block'>
                                <Link className='forget-password'>Forgot Password?</Link>
                            </div>
                            <Link to='/screen2'>
                                <Button type="submit" className='sign-in-btn' onClick={handleSubmit}>Sign in</Button>
                            </Link>
                        </Form>
                        <span className='devider'>or continue with</span>
                        <div className='continue-signin-group'>
                            <Button target='_blank'><img src={facebook} alt="facebook" className='img-fluid' /></Button>
                            <Button target='_blank'><img src={google} alt="facebook" className='img-fluid' /></Button>
                            <Button target='_blank'><img src={apple} alt="facebook" className='img-fluid' /></Button>
                        </div>
                        <div className='sign-up-block'>
                            <p>Dont have an account? <Link to="/register">Sign up</Link></p>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Login
