import React, { useEffect, useState } from 'react'

import moment from "moment"
import burgerIcon from '../../../assets/images/burger-icon.svg';
import closeIcon from '../../../assets/images/close-icon.svg';
import logo from '../../../assets/images/logo.png';
// import manchesterlogo from '../../../assets/images/manchesterlogo.png';
// import reallogo from '../../../assets/images/reallogo.png';
import SideMenu from '../../../components/SideMenu';

import './index.css';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

const FreePoint = () => {
    // const dispatch = useDispatch();
    // const navigate = useNavigate();
    const { user } = useSelector((state) => state.auth);
    const { playList, team1Idx, eventDate } = useSelector((state) => state.sport);

    const [menuOpen, setMenuOpen] = useState(false);

    const handleMenuToggle = () => {
        setMenuOpen(!menuOpen);
    };

    useEffect(() => {
        if (menuOpen) {
            document.body.style.overflowX = 'hidden';
            document.body.style.overflowY = 'hidden';
            document.documentElement.style.overflowX = 'hidden';
            document.documentElement.style.overflowY = 'hidden';
        } else {
            document.body.style.overflow = '';
            document.documentElement.style.overflow = '';
        }
    }, [menuOpen]);

    return (
        <>
            <div className={`screen-wrapper ${menuOpen ? 'menu-open' : ''}`}>
                <div className='burger-icon' onClick={handleMenuToggle}>
                    <img src={menuOpen ? closeIcon : burgerIcon} alt="menu toggle" />
                </div>
                {/* <div className='srbook'>
                    <img src={srbook} alt="book" className='img-fluid' />
                    <span>House Rules</span>
                </div> */}
                <div className='screen-4-section'>
                    <div className='screen-4-content'>
                        <div className='screen-top-part'>
                            <div className='logo-part'>
                                <a href='/'>
                                    <img src={logo} alt="logo" className='img-fluid' />
                                </a>
                            </div>
                            <span className='screen-title'>Your Game</span>
                            <div className='select-team-group'>
                                <div className='select-team'>
                                    <div className='team-image'>
                                        {playList.boxscore ? <img src={playList.boxscore.teams[0].team.logo} alt={playList.boxscore && playList.boxscore.teams[0].team.shortDisplayName} style={{ width: 43, height: 43 }} /> : <div></div>}
                                    </div>
                                    <span>{playList.boxscore && playList.boxscore.teams[0].team.shortDisplayName}</span>
                                </div>
                                <div className='opposite-part'>vs <span>{eventDate && moment(eventDate).format("h:mm")}</span></div>
                                <div className='select-team'>
                                    <div className='team-image'>
                                        {playList.boxscore ? <img src={playList.boxscore.teams[1].team.logo} alt={playList.boxscore && playList.boxscore.teams[1].team.shortDisplayName} style={{ width: 43, height: 43 }} /> : <div></div>}
                                    </div>
                                    <span>{playList.boxscore && playList.boxscore.teams[1].team.shortDisplayName}</span>
                                </div>
                            </div>
                            <div className='your-team-block'>
                                <div className='your-team'>
                                    <h4>Your Team</h4>
                                </div>
                                <div className='select-team'>
                                    <div className='team-image'>
                                        {playList.boxscore ? <img src={playList.boxscore.teams[team1Idx].team.logo} alt={playList.boxscore && playList.boxscore.teams[team1Idx].team.shortDisplayName} style={{ width: 27, height: 27 }} /> : <div></div>}
                                    </div>
                                    <span>{playList.boxscore && playList.boxscore.teams[team1Idx].team.shortDisplayName}</span>
                                </div>
                            </div>
                        </div>
                        <div className='play-free-game-block'>
                            <span className='screen-purple-glow-title'>Your first game is <span>FREE!</span></span>
                            <Link to="/play" className='chance-to-win-block'>
                                <span className='free-info'>Free</span>
                                <div className='chance-to-win-info'>
                                    <p>Play for a chance
                                        <span>to Win $25</span></p>
                                </div>
                            </Link>
                            <div className='no-thanks-btn-block'>
                                <Link to="/play">No Thanks</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Side Menu */}
            <SideMenu
                menuOpen={menuOpen}
                user={user}
            />
        </>
    )
}

export default FreePoint
