import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { Accordion, Button, Form } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';

import './index.css';
import burgerIcon from '../../../assets/images/burger-icon.svg';
import closeIcon from '../../../assets/images/close-icon.svg';
import logo from '../../../assets/images/logo.png';
import venmologo from '../../../assets/images/venmologo.png';
import paypallogo from '../../../assets/images/paypallogo.png';
import squarelogo from '../../../assets/images/squarelogo.png';
import zelle from '../../../assets/images/zelle.png';
import venmo from '../../../assets/images/venmo.svg';
import paypal from '../../../assets/images/paypal.svg';
import cashapp from '../../../assets/images/cashapp.svg';
import zellelogo from '../../../assets/images/zellelogo.svg';
import editpen from '../../../assets/images/editpen.svg';
import SideMenu from '../../../components/SideMenu';

const Payments = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { user } = useSelector((state) => state.auth);

    const [menuOpen, setMenuOpen] = useState(false);

    const handleMenuToggle = () => {
        setMenuOpen(!menuOpen);
    };

    useEffect(() => {
        if(!isNaN(user.payment)){
            navigate('/play');
        } else {
            if (!isNaN(user.payment.venmo) || !isNaN(user.payment.paypal) || !isNaN(user.payment.cashApp) || !isNaN(user.payment.zelle)) {
                navigate('/play');
            }
        }
    }, [])

    useEffect(() => {
        if (menuOpen) {
            document.body.style.overflowX = 'hidden';
            document.body.style.overflowY = 'hidden';
            document.documentElement.style.overflowX = 'hidden';
            document.documentElement.style.overflowY = 'hidden';
        } else {
            document.body.style.overflow = '';
            document.documentElement.style.overflow = '';
        }
    }, [menuOpen]);

    // Set the default active key to "0"
    const [activeKey, setActiveKey] = useState('0');

    const handleAccordionChange = (eventKey) => {
        // Toggle the accordion open/close based on the eventKey
        setActiveKey(eventKey === activeKey ? null : eventKey);
    };

    const handlePlayGame = () => {
        navigate('/play');
    }

    return (
        <>
            <div className={`screen-wrapper ${menuOpen ? 'menu-open' : ''}`}>
                <div className='burger-icon' onClick={handleMenuToggle}>
                    <img src={menuOpen ? closeIcon : burgerIcon} alt="menu toggle" />
                </div>
                <div className='screen-5-section'>
                    <div className='screen-5-content'>
                        <div className='screen-top-part'>
                            <div className='logo-part'>
                                <img src={logo} alt="logo" className='img-fluid' />
                            </div>
                            <span className='screen-title'>Add a Payout Source</span>
                        </div>
                        <div className='payment-method-block'>
                            <div className='payment-method-list'>
                                <ul>
                                    <li><Link><img src={venmologo} alt="paymentlogo" className='img-fluid' /></Link></li>
                                    <li><Link><img src={paypallogo} alt="paymentlogo" className='img-fluid' /></Link></li>
                                    <li><Link><img src={squarelogo} alt="paymentlogo" className='img-fluid' /></Link></li>
                                    <li><Link><img src={zelle} alt="paymentlogo" className='img-fluid' /></Link></li>
                                </ul>
                            </div>
                            <div className='payment-method-process'>
                                <Accordion activeKey={activeKey} onSelect={handleAccordionChange}>
                                    <Accordion.Item eventKey="0" className={`accordion-item-custom ${activeKey === '0' ? 'expanded' : ''}`}>
                                        <Accordion.Header> <img src={venmo} alt="venmo" className='img-fluid' /> <span>Preferred</span> </Accordion.Header>
                                        <Accordion.Body>
                                            <Form>
                                                <Form.Group controlId="formGroupText" className='form-group'>
                                                    <Form.Control type="text" placeholder="Add your Venmo Username" />
                                                    <img src={editpen} alt="edit" className='img-fluid' />
                                                </Form.Group>
                                            </Form>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="1" className={`accordion-item-custom ${activeKey === '1' ? 'expanded' : ''}`}>
                                        <Accordion.Header><img src={paypal} alt="paypal" className='img-fluid' /></Accordion.Header>
                                        <Accordion.Body>
                                            <Form>
                                                <Form.Group controlId="formGroupText" className='form-group'>
                                                    <Form.Control type="text" placeholder="Add your Venmo Username" />
                                                    <img src={editpen} alt="edit" className='img-fluid' />
                                                </Form.Group>
                                            </Form>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="2" className={`accordion-item-custom ${activeKey === '2' ? 'expanded' : ''}`}>
                                        <Accordion.Header><img src={cashapp} alt="cashapp" className='img-fluid' /></Accordion.Header>
                                        <Accordion.Body>
                                            <Form>
                                                <Form.Group controlId="formGroupText" className='form-group'>
                                                    <Form.Control type="text" placeholder="Add your Venmo Username" />
                                                    <img src={editpen} alt="edit" className='img-fluid' />
                                                </Form.Group>
                                            </Form>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="3" className={`accordion-item-custom ${activeKey === '3' ? 'expanded' : ''}`}>
                                        <Accordion.Header><img src={zellelogo} alt="zelle" className='img-fluid' /></Accordion.Header>
                                        <Accordion.Body>
                                            <Form>
                                                <Form.Group controlId="formGroupText" className='form-group'>
                                                    <Form.Control type="text" placeholder="Add your Venmo Username" />
                                                    <img src={editpen} alt="edit" className='img-fluid' />
                                                </Form.Group>
                                            </Form>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                                <Button className='submit-btn' onClick={() => handlePlayGame()}>Submit</Button>
                                <span className='payout-process-time'>Payouts are processed on Mondays</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Side Menu */}
            <SideMenu
                menuOpen={menuOpen}
                user={user}
            />
        </>
    )
}

export default Payments
