import React, { useState, useEffect } from 'react';
import logo from '../../../assets/images/logo.png';

import moneywave from '../../../assets/images/moneywave.svg';
import burgerIcon from '../../../assets/images/burger-icon.svg';
import closeIcon from '../../../assets/images/close-icon.svg';
import './index.css';
import { Button, Col, Row } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';

import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';

import { SPORTS_CATEGORY, URL } from '../../../const.js';
import SideMenu from '../../../components/SideMenu.js';
import { SPORT_ADD_REQUEST } from "../../../store/actionTypes.js";

const Sports = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.auth);

  const [menuOpen, setMenuOpen] = useState(false);
  const [team1Idx, setTeam1Idx] = useState(-1);
  const [sportCategory, setSportCategory] = useState('NFL');
  const [events, setEvents] = useState([]);
  const [playList, setPlayList] = useState([]);
  const [eventId, setEventId] = useState(-1);
  const [eventDate, setEventDate] = useState();

  const [primaryColor, setPrimaryColor] = useState('#0d6efd')
  const [secondaryColor, setSecondaryColor] = useState('white')

  const handleMenuToggle = () => {
    setMenuOpen(!menuOpen);
  };

  useEffect(() => {
    if (menuOpen) {
      document.body.style.overflowX = 'hidden';
      document.body.style.overflowY = 'hidden';
      document.documentElement.style.overflowX = 'hidden';
      document.documentElement.style.overflowY = 'hidden';
    } else {
      document.body.style.overflow = '';
      document.documentElement.style.overflow = '';
    }
  }, [menuOpen]);

  /* Get Total Event */
  useEffect(() => {
    if (sportCategory) {
      let apiUrl = URL[sportCategory + '_TODAY_EVENT'];
      axios.get(apiUrl,
      ).then((response) => {
        setEvents(response.data.events);
      }).catch((err) => {
        console.log(err);
      });
    }
  }, [sportCategory])

  useEffect(() => {
    let apiUrl = URL[sportCategory];
    if (eventId != -1) {
      axios.get(apiUrl,
        {
          params: {
            event: eventId
          }
        }
      ).then((response) => {
        setPlayList(response.data);
      })
    }
  }, [eventId])

  const handleEventId = (item) => {
    setEventId(item.id);
    setPlayList([])
    setEventDate(item.date);
    console.log(item.id, 'event id');
  };

  const handleTeam = (index) => {
    setTeam1Idx(index);
    var primary_col = playList.boxscore.teams[index].team.color;
    var secondary_col = playList.boxscore.teams[index].team.alternateColor;
    if (!primary_col.includes('#')) {
      setPrimaryColor('#' + primary_col)
    }

    if (!secondary_col.includes('#')) {
      setSecondaryColor('#' + secondary_col)
    }
  };

  const selectSport = (sport) => {
    setSportCategory(sport)
  }

  const handlePlayGame = (index) => {
    if (eventId == -1) {
      toast('Please select game')
      return;
    }

    if (team1Idx == -1) {
      toast('Please select team')
      return;
    }

    if (index === 1) {
      navigate('/point/select')
      dispatch({ type: SPORT_ADD_REQUEST, payload: { sportCategory, eventId, team1Idx, eventDate, playList, primaryColor, secondaryColor } })
    } else {
      navigate('/point/free')
      dispatch({ type: SPORT_ADD_REQUEST, payload: { sportCategory, eventId, team1Idx, eventDate, playList, primaryColor, secondaryColor } })
    }
  }

  return (
    <>
      <div className={`screen-wrapper ${menuOpen ? 'menu-open' : ''}`}>
        <div className='burger-icon' onClick={handleMenuToggle}>
          <img src={menuOpen ? closeIcon : burgerIcon} alt="menu toggle" />
        </div>
        <div className='screen-2-section'>
          <div className='screen-2-content'>
            <div className='screen-top-part'>
              <div className='logo-part'>
                <img src={logo} alt="logo" className='img-fluid' />
              </div>
              <span className='screen-title'>Pick a Sport</span>
              <div className='sport-list'>
                <ul>
                  {
                    SPORTS_CATEGORY.map((item, key) => {
                      return <li key={key} onClick={() => selectSport(item.value)}><Link className={sportCategory == item.value && 'selected-category-link'}>{item.label}</Link></li>
                    })
                  }
                </ul>
              </div>
            </div>
            <div className='screen-2-centerpart'>
              <span className='screen-purple-glow-title'>Pick a Game</span>
              <div className='screen-2-center-block'>
                <Row>
                  {events.map((item, index) => (
                    <Col xs={6} key={index}>
                      <div
                        className={`screen-2-center-detail ${eventId === item.id ? 'selected' : ''}`}
                        onClick={() => handleEventId(item)}
                      >
                        <img src={item.competitions && item.competitions[0].competitors[1].team.logo} alt="team 1" className='img-fluid' style={{ width: 40, height: 40 }} />
                        <span>vs</span>
                        <img src={item.competitions && item.competitions[0].competitors[0].team.logo} alt="team 2" className='img-fluid' style={{ width: 40, height: 40 }} />
                      </div>
                    </Col>
                  ))}
                </Row>
              </div>
            </div>
            <div className='screen-2-bottom-part'>
              <span className='screen-purple-glow-title'>Pick a Team</span>
              <div className='screen-2-bottom-block'>
                <div
                  className={`screen-2-bottom-name-block ${team1Idx === 0 ? 'selected' : ''}`}
                  onClick={() => handleTeam(0)}
                >
                  <div className='screen-2-team-name'>
                    {playList.boxscore ? <img src={playList.boxscore.teams[0].team.logo} alt={playList.boxscore && playList.boxscore.teams[0].team.shortDisplayName} style={{ width: 60, height: 60 }} /> : <div></div>}
                  </div>
                  <span className='screen-2-team-name-title'>{playList.boxscore && playList.boxscore.teams[0].team.shortDisplayName}</span>
                </div>
                <span className='screen-2-opposite-title'>vs</span>
                <div
                  className={`screen-2-bottom-name-block ${team1Idx === 1 ? 'selected' : ''}`}
                  onClick={() => handleTeam(1)}
                >
                  <div className='screen-2-team-name'>
                    {playList.boxscore ? <img src={playList.boxscore.teams[1].team.logo} alt={playList.boxscore && playList.boxscore.teams[1].team.shortDisplayName} style={{ width: 60, height: 60 }} /> : <div></div>}
                  </div>
                  <span className='screen-2-team-name-title'>{playList.boxscore && playList.boxscore.teams[1].team.shortDisplayName}</span>
                </div>
              </div>
            </div>
            <div className='screen-2-bottom-button'>
              <Button onClick={() => handlePlayGame(1)}>
                <img src={moneywave} alt="moneywave" className='img-fluid' />
                Play For Money
              </Button>
              <Button onClick={() => handlePlayGame(2)}>Play For Fun</Button>
            </div>
          </div>
        </div>
      </div>

      {/* Side Menu */}
      <SideMenu
        menuOpen={menuOpen}
        user={user}
      />
    </>
  );
};

export default Sports;





