import React, { useEffect, useState } from 'react'

import logo from '../../assets/images/logo.png';
import burgerIcon from '../../assets/images/burger-icon.svg';
import closeIcon from '../../assets/images/close-icon.svg';
import screen7gameimg from '../../assets/images/screen7gameimg.png';
import playbutton from '../../assets/images/playbutton.svg';
import football from '../../assets/images/football.svg';
import dallaslogo from '../../assets/images/dallaslogo.svg';
import dollar from '../../assets/images/dollar.svg';
import message from '../../assets/images/message.svg';
import lock from '../../assets/images/lock.svg';
import phone from '../../assets/images/phone.svg';
import username from '../../assets/images/username.svg';
import hide from '../../assets/images/hide.svg';

import './index.css';
import { Link, useNavigate } from 'react-router-dom';
import { Form, Button } from 'react-bootstrap';
import SideMenu from '../../components/SideMenu';
import { useDispatch, useSelector } from 'react-redux';

const Screen7 = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const { user } = useSelector((state) => state.auth);

    const [menuOpen, setMenuOpen] = useState(false);

    const handleMenuToggle = () => {
        setMenuOpen(!menuOpen);
    };

    useEffect(() => {
        if (menuOpen) {
            document.body.style.overflowX = 'hidden';
            document.body.style.overflowY = 'hidden';
            document.documentElement.style.overflowX = 'hidden';
            document.documentElement.style.overflowY = 'hidden';
        } else {
            document.body.style.overflow = '';
            document.documentElement.style.overflow = '';
        }
    }, [menuOpen]);

    return (
        <>
            <div className={`screen-wrapper ${menuOpen ? 'menu-open' : ''}`}>
                <div className='burger-icon' onClick={handleMenuToggle}>
                    <img src={menuOpen ? closeIcon : burgerIcon} alt="menu toggle" />
                </div>
                <div className='screen-7-section'>
                    <div className='screen-7-content'>
                        <div className='screen-gameimgpart'>
                            <div className='screen-gameimage'>
                                <img src={screen7gameimg} alt="gameimg" className='img-fluid' />
                            </div>
                            <div className='screen-logo-part'>
                                <div className='screen-logo-image'>
                                    <img src={logo} alt="logo" className='img-fluid' />
                                </div>
                                <div className='demo-button'>
                                    <img src={playbutton} alt="play" className='img-fluid' />
                                </div>
                                <div className='screen-7-title'>
                                    <h3>See A Demo Game</h3>
                                </div>
                            </div>
                        </div>
                        <div className='screen-7-help-part'>
                            <div className='screnn-7-help-title'>
                                <h4>HOW DO I PLAY?</h4>
                            </div>
                            <div className='timeline-process'>
                                <div className='timeline-process-info'>
                                    <div className='timeline-process-image'>
                                        <img src={football} alt="football" className='img-fluid' />
                                    </div>
                                    <span className='timeline-process-content'>Choose a Game
                                        <br />to watch</span>
                                </div>
                                <div className='timeline-process-info'>
                                    <div className='timeline-process-image'>
                                        <img src={dallaslogo} alt="football" className='img-fluid' />
                                    </div>
                                    <span className='timeline-process-content'>Choose a Team
                                        <br />to root for</span>
                                </div>
                                <div className='timeline-process-info'>
                                    <div className='timeline-process-image'>
                                        <img src={dollar} alt="football" className='img-fluid' />
                                    </div>
                                    <span className='timeline-process-content'>Enjoy,
                                        <br />It's that easy!</span>
                                </div>
                            </div>
                            <div className='help-info-content'>
                                <p><span>BALLHOG</span> uses a unique rotational scoring system that makes playing as  easy as watching your favorite team play. No spreads, no over/unders, no  bad beats, no wrong decisions. Because you play to win, our one of a  kind scoring formate makes every play matter and every game a party.</p>
                            </div>
                        </div>
                        <div className='start-playing-part'>
                            <span className='screen-purple-glow-title'>START PLAYING TODAY</span>
                            <div className='start-playing-ballhog'>
                                <p><span>BALLHOG</span> is currently in beta mode. <strong>Sign up</strong> to play see what makes <span>BALLHOG</span> different.</p>
                            </div>
                            <div className='screen-form'>
                                <Form>
                                    <Form.Group controlId="formGroupNumber" className='form-group'>
                                        <Form.Control type="number" placeholder="Phone Number" />
                                        <img src={phone} alt="phone" className='img-fluid' />
                                    </Form.Group>
                                    <Form.Group controlId="formGroupEmail" className='form-group'>
                                        <Form.Control type="email" placeholder="Email Address" />
                                        <img src={message} alt="message" className='img-fluid' />
                                    </Form.Group>
                                    <Form.Group controlId="formGroupText" className='form-group'>
                                        <Form.Control type="text" placeholder="Username" />
                                        <img src={username} alt="message" className='img-fluid' />
                                    </Form.Group>
                                    <Form.Group controlId="formGroupPassword" className='form-group'>
                                        <Form.Control type="password" placeholder="Password" />
                                        <img src={lock} alt="lock" className='img-fluid' />
                                        <img src={hide} alt="hide" className='img-fluid hide-password' />
                                    </Form.Group>
                                    <Link to='/screen8'>
                                        <Button type="submit" className='sign-in-btn'>Submit</Button>
                                    </Link>
                                </Form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Side Menu */}
            <SideMenu
                menuOpen={menuOpen}
                user={user}
            />
        </>
    )
}

export default Screen7
