import { SPORT_ADD_REQUEST, SPORT_UPDATE_REQUEST, SPORT_DELETE_REQUEST } from "../actionTypes";

const initialState = {
  point: null,
  eventId: -1,
  sportCategory: null,
  team1Idx: -1,
  eventDate: null,
  playList: [],
  primaryColor: '#0d6efd',
  secondaryColor: 'white'
};

const sportReducer = (state = initialState, action) => {
  switch (action.type) {
    case SPORT_ADD_REQUEST:
      return { ...state, ...action.payload };
    case SPORT_UPDATE_REQUEST:
      return { ...state, ...action.payload };
    case SPORT_DELETE_REQUEST:
      return { ...state, user: null, eventId: null, sportCategory: null, team1Id: null, eventDate: null, playList: [], primaryColor: '#0d6efd', secondaryColor: 'white' };

    default:
      return state;
  }
};

export default sportReducer;
